.Root {
    text-align: center;
    height: 100%;
    overflow: hidden;
}

.Root__comingSoon {
    height: 100vh;
    width: 100vw;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    text-align: center;
    font-size: 30px;
    font-family: "Beleren Bold";
    color: #eee;
}

.Root__preloader {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: #333;
    position: fixed;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
    z-index: 1500;
}

.Root__circular {
    margin: 60px 0;
}

.Root__linear {
    margin: 0 30px;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}



